@import "../../App.scss";

.Footer {
  background-color: $main-black;
  color: $main-white;
  padding: 40px 0 0;
  font-size: 16px;

  .footer-content {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    padding: 0 40px;

    .footer-section {
      flex: 1;
      min-width: 250px;
      margin: 20px;

      h1,
      h2 {
        color: $main-blue;
        margin-bottom: 10px;
      }

      p {
        line-height: 1.6;
        color: $main-white;
      }

      .logo-text {
        font-size: 24px;
        font-weight: bold;
        color: $main-yellow;
      }

      .contact span {
        display: block;
        margin-top: 10px;
      }

      ul {
        list-style: none;
        padding: 0;

        li {
          margin: 8px 0;

          a {
            color: $main-white;
            text-decoration: none;

            &:hover {
              text-decoration: underline;
              color: $main-yellow;
            }
          }
        }
      }

      form {
        display: flex;
        flex-direction: column;

        .text-input {
          padding: 10px;
          margin-bottom: 10px;
          border: none;
          border-radius: 5px;
          background: #f4f4f4;
          font-size: 14px;
          color: $main-black;
        }

        .btn {
          background-color: $main-blue;
          color: $main-white;
          padding: 10px;
          border: none;
          border-radius: 5px;
          font-weight: bold;
          cursor: pointer;
          transition: background-color 0.3s;

          &:hover {
            background-color: darken($main-blue, 10%);
          }
        }
      }
    }
  }

  .footer-bottom {
    background-color: #1f1f1f;
    color: #bbb;
    padding: 16px 0;
    text-align: center;
    font-size: 14px;
    margin-top: 20px;
  }
}

// Responsive
@media (max-width: 768px) {
  .Footer {
    .footer-content {
      flex-direction: column;
      align-items: center;
      padding: 0 20px;
    }

    .footer-section {
      margin: 20px 0;
    }
  }
}
