@import "../../App.scss";

.Homepage {
  padding: 30px 16px;
  font-size: 18px;
  color: $main-black;

  h2,
  .homepage-title {
    font-size: 28px;
    color: $main-black;
    text-align: center;
    animation: language-description-title-effect 1.8s ease-in-out;

    .highlight {
      color: $main-blue;
    }
  }

  .homepage-intro {
    margin: 16px 0;
    text-align: center;
    font-size: 18px;
    line-height: 1.6;
    animation: description-first-effect 0.8s ease-in-out;

    strong {
      display: inline-block;
      margin-right: 6px;
      color: $main-blue;
    }
  }

  .section-title {
    margin: 40px 0 16px;
    font-size: 24px;
    color: $main-blue;
    text-align: center;
  }

  .example-table {
    margin: 20px auto;
    width: 100%;
    max-width: 500px;
    border-collapse: collapse;
    border: 1px solid #ccc;
    font-size: 17px;

    thead {
      background-color: $main-blue;
      color: $main-white;
    }

    th,
    td {
      padding: 10px;
      text-align: left;
      border: 1px solid #ddd;
    }

    tbody tr:nth-child(odd) {
      background-color: #f9f9f9;
    }

    tbody tr:nth-child(even) {
      background-color: #ffffff;
    }
  }
}
